<template>
 <div>
    
  <v-menu
    bottom
    left
    min-width="200"
    offset-y
    origin="top right"
    transition="scale-transition"
  >
    <template v-slot:activator="{ attrs, on }">
      <v-btn class="ml-2" min-width="0" text v-bind="attrs" v-on="on">
        <v-icon>mdi-account</v-icon><b class="">{{ userDetails.full_name }}</b>
      </v-btn>
        <v-btn class="ml-2" min-width="0" text v-bind="attrs">
          <v-icon>mdi-wallet</v-icon><b class="">Wallet: XAF {{walletBalance}}</b>
        </v-btn>
    </template>

    <v-list :tile="false" flat nav>
      <template>
        <app-bar-item to="/">
          <v-list-item-title
            ><v-btn @click="logout()">Logout</v-btn></v-list-item-title
          >
        </app-bar-item>
      </template>
    </v-list>
  </v-menu>
 </div>
</template>

<script>
import { mapGetters } from "vuex";
import axios from "axios";
let piCredentials = {
  username: "pidigitadsignage",
  password: "Nelson@Akem@1000.",
};
let digitadApps = {
  users: "https://app.digitadooh.com", //https://digitad.us-east-2.elasticbeanstalk.com
  piApp: "https://ourcore.digitadooh.com",
};
export default {
  name: "DefaultAccount",
  methods: {
    logout() {
      const params = { crossDomain: true, withCredentials:false};
      const api = axios.create({ baseURL: digitadApps.users });
      api
        .get(digitadApps.users + "/server/logout.php", params)
        .then((result) => {
          let feedback = result.data;
          console.log(feedback);
          if (feedback !== 0) {
             window.location = "https://app.digitadooh.com/login";
          } else {
            window.location = "https://app.digitadooh.com/login";
          }
        })
        .catch((err) => {
          //window.location = digitadApps.users + "/login";
          console.log(err);
        });
    },
    authenticateClient() {
      const params = { crossDomain: true, withCredentials: true };
      const api = axios.create({ baseURL: digitadApps.users });
      api
        .get(digitadApps.users + "/server/user_info.php", params)
        .then((result) => {
          let user_info = result.data;
          //console.log(user_info);
          if (user_info !== 0) {
            this.userDetails.user_id = user_info.id;
            this.userDetails.f_name = user_info.f_name;
            this.userDetails.l_name = user_info.l_name;
            this.userDetails.email = user_info.email;
            this.userDetails.country = user_info.country;
            this.userDetails.companyName = user_info.companyName;
            this.userDetails.city = user_info.city;
            this.userDetails.homeAddress = user_info.user_address;
            this.userDetails.position_in_company =
              user_info.position_in_company;
            this.userDetails.companyAddress = user_info.company_address;
            this.userDetails.company_reg_num = user_info.company_reg_num;
            this.userDetails.full_name =
              user_info.f_name + " " + user_info.l_name;
          } else {
           //window.location = "https://app.digitadooh.com/login";
          }
        })
        .catch((err) => {
          //window.location = "https://app.digitadooh.com/login";
          console.log(err);
        });
    },
    fetch_user_campaigns() {
      const params = { crossDomain: true, withCredentials: true };
      const api = axios.create({ baseURL: digitadApps.users });
      api
        .get(digitadApps.users + "/server/fetch_campaigns.php", params)
        .then((result) => {
          let campaigns = result.data;
          if (campaigns.length > 0) {
            campaigns.forEach((campaign) => {
              console.log(campaign);
              if (campaign.approved == 0) {
                this.unApprovedCampaigns.push(campaign);
              } else if (campaign.approved == 1 && campaign.paid == 0) {
                this.not_paid_campaigns.push(campaign);
                this.library_campaigns.push(campaign);
              } else if (campaign.approved == 1 && campaign.paid == 1) {
                this.activeCampaigns.push(campaign);
                this.library_campaigns.push(campaign);
              }else if (campaign.approved == 2) {
                this.rejectedCampaign.push(campaign);
              }
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    fetch_history() {
      const params = { crossDomain: true, withCredentials: true };
      const api = axios.create({ baseURL: digitadApps.users });
      api
        .get(digitadApps.users + "/server/fetch_history.php", params)
        .then((result) => {
          let campaigns = result.data;
          if (campaigns.length > 0) {
            campaigns.forEach((campaign) => {
              //push campaigns into history store
              this.history.push(campaign);
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    fetchArchives() {
      const params = { crossDomain: true, withCredentials: true };
      const api = axios.create({ baseURL: digitadApps.users });
      api
        .get(digitadApps.users + "/server/fetch_archives.php", params)
        .then((result) => {
          let campaigns = result.data;
          //console.log(campaigns);
          if (campaigns.length > 0) {
            campaigns.forEach((campaign) => {
              //push to archive store
              this.archivedCampaigns.push(campaign);
              this.library_campaigns.push(campaign);
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getWallet(){
      const params = { crossDomain: true, withCredentials: true };
      const api = axios.create({ baseURL: digitadApps.users });
       api
        .get(digitadApps.users + "/server/getWallet.php", params)
        .then((result)=>{
          //console.log(result.data);
          if(result.data.success==1){
            this.wallet.push(result.data);
            this.walletBalance=result.data.balance;
          }
        })
        .catch((error)=>{
          console.log(error);
        })
    },
    fetchScreens() {
      const params = { crossDomain: true, withCredentials: true };
      const api = axios.create({ baseURL: digitadApps.users });
      api
        .get(digitadApps.users + "/server/fetch_screens.php", params)
        .then((result) => {
          let screens = result.data;
          //console.log(screens);
          if (screens.length > 0) {
            //set premium data
            let currentCity="";
            screens.forEach((screen) => {
               let city=screen.city;
               //PUSH DIFFERENT CITIES INTO ARRAY
              if(city.toLowerCase()!==currentCity.toLowerCase()){
                this.premium_cities.push(city);
                //push into premium data
                this.premiumCityScreens.push(screen);
                //set value of currentCity to city
                currentCity=city;
              }
              //push to archive store
              this.screensInfo.push(screen);
            });
            //
            this.screenCities.push("Default");
            this.screenStates.push("Default");
            for (let i = 0; i < screens.length; i++) {
              let city = screens[i].city;
              let state = screens[i].state;
              this.screenCities.push(city);
              this.screenStates.push(state);
              this.screen_checkboxStates.push(0);
            }
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },

  data: () => ({
    profile: [
      { title: "Profile" },
      { title: "Settings" },
      { divider: true },
      { title: "Log out" },
    ],
    credentials: {
      name: "",
      email: "",
    },
    walletBalance:0,
  }),
  mounted() {
    this.getWallet();
    //authenticate client
    this.authenticateClient();
    //fetch all user campaigns
    this.fetch_user_campaigns();
    //fetch history
    this.fetch_history();
    //fetch archives
    this.fetchArchives();
    //fetch all screens
    this.fetchScreens();
  },
  computed: mapGetters([
    "activeCampaigns",
    "rejectedCampaign",
    "archivedCampaigns",
    "unApprovedCampaigns",
    "not_paid_campaigns",
    "userDetails",
    "library_campaigns",
    "screensInfo",
    "screenCities",
    "screenStates",
    "screen_checkboxStates",
    "wallet",
    "premium_cities",
    "premiumCityScreens",
    "history"
  ]),
};
</script>
